
.component.files {

    body & {
        @media only screen and (min-width: 1px) {

          padding-top: 15px;

          table {

            td{
              vertical-align: middle;
            }

            .icon-section {
              text-align: center;
              background: @lt-green;
              border-color: darken(@lt-green,15%);
              width: 35px;

              a {
                .inline;
                text-decoration: none !important;

                span {
                  .font-size(30px, 1);
                  color: lighten(@lt-green, 15%);
                }

                &:active span{
                  text-decoration: none;
                  color: @white;
                }
              }
            }

            .label-section {
              color: @blue;

              a{
                &:active{
                  color: @lt-green;
                }
              }
            }

            .view-section {
              width: 95px;
            }
          }
        }

        @media only screen and (min-width: @screen-sm-min) {
        }

        @media only screen and (min-width: @screen-md-min) {

          .icon-section {

            a {

              span{
                .lh-transition(color .2s linear);
              }

              &:hover{

                span{
                  color: @white;
                }
              }
            }
          }

          .label-section {

            a{

              .hover-link(@lt-green);
            }
          }

          .view-section{

            a{

            }
          }
        }

        @media only screen and (min-width: @screen-lg-min) {
        }
    }
}