.component.video-thumbnail {

    body & {
        @media only screen and (min-width: 1px) {

          max-width: 200px;
          .inline(middle);
          position: relative;

          .thumb{
            position: relative;
            z-index: 0;
            .lh-opacity(0.4);
            line-height: 0;
            overflow: hidden;
          }

          .icon{
            cursor: pointer;
          }

          .text{
            .font-1;
            position: absolute;
            bottom: 3px;
            padding: 0px 5px;
            font-style: italic;
            z-index: 1;
            cursor: pointer;
          }

          &.blue{

            background: @blue;

            .text{
              color: @white;
            }

            .icon{
              .video-icon(@lt-green, @white, @white);
              z-index: 2;
            }
          }

          &.white{
            background: @white;

            .text{
              color: @blue;
            }

            .icon{
              .video-icon(@lt-green, @blue, @blue);
              z-index: 2;
            }
          }

        }

        @media only screen and (min-width: @screen-sm-min) {

          .text{
            .font-size(11px, 11px);
          }
        }

        @media only screen and (min-width: @screen-md-min) {

          .thumb{
            cursor: pointer;

            img{
              .lh-transition(all .2s ease);
            }
          }

          .text{
            .font-size(12px, 17px);
          }

          .icon{
            .lh-transition(transform .5s ease);
          }

          &:hover{

            .thumb{

              img{
                .lh-scale(1.2);
              }
            }

            .icon{
              .lh-rotate(360deg);
            }
          }
        }

        @media only screen and (min-width: @screen-lg-min) {
        }
    }
}