body {
  @media only screen and (min-width: 1px){

    *:focus{
      outline: none !important;
    }

    .main-section{
      .roboto-slab;
    }

    h1,h2,h3,h4{
      margin: 0;
      line-height: 1.25em;
    }

    h1{
      .font-5;
      .color-blue;
    }
    h2{
      .font-4;
    }
    h3{
      .font-3;
      .color-blue;
    }
    h4{
      .font-2;
    }

    img{
      max-width: 100%;
    }

    .form-control{
      .lh-border-radius(0) !important;
      .form-control-focus(@lt-green);
    }

    hr{
      .vertical-margin;
    }

    .the-subhead{
      color: @lt-green;
      .font-weight(600);
      .font-3;
    }

    .phone-link{
      .phone-link(@dk-blue);
    }

    //REMOVE WEBKIT APPEARANCE
    *{
      -webkit-appearance: none;
    }

    //SELECTION
    @select-green: lighten(@lt-green, 20%);
    ::selection {
        background: @select-green; /* Safari */
    }
    ::-moz-selection {
        background: @select-green; /* Firefox */
    }

    //BUTTONS
    .btn{
      .roboto;
      border: 0 !important;
      .lh-border-radius(0) !important;

      &.submit-btn {
        width: 100%;
      }
      &.btn-black{
        background: @black;
        color: @white;

        &:before{
          background: lighten(@black, 10%);
        }
      }
      &.btn-white{
        background: @white;
        color: @blue;

        &:before{
          background: darken(@white, 10%);
        }
      }
      &.btn-grey{
        background: @grey;
        color: @black;

        &:before{
          background: lighten(@grey, 10%);
        }
      }
      &.btn-lt-grey{
        background: @lt-grey;
        color: @blue;

        &:before{
          background: darken(@lt-grey, 10%);
        }
      }
      &.btn-ltst-grey{
        background: @ltst-grey;
        color: @blue;

        &:before{
          background: darken(@ltst-grey, 10%);
        }
      }
      &.btn-green{
        background: @green;
        color: @white;

        &:before{
          background: lighten(@green, 10%);
        }
      }
      &.btn-lt-green{
        background: @lt-green;
        color: @white;

        &:before{
          background: darken(@lt-green, 10%);
        }
      }
      &.btn-dk-blue{
        background: @dk-blue;
        color: @white;

        &:before{
          background: lighten(@dk-blue, 10%);
        }
      }
      &.btn-blue,
      &.btn-default{
        background: @blue;
        color: @white;

        &:before{
          background: lighten(@blue, 10%);
        }
      }
    }

    //CHEVRONS

    .chevrons{
      .inline(middle);
      line-height: 0;

      &.position-right { margin-left: 5px;}
      &.position-left { margin-right: 5px;}

      img{
        .inline(middle);
        height: auto;
      }

      &.position-down{
        img{
          .lh-rotate(90deg);
        }
      }
      &.position-left{
        img{
          .lh-rotate(180deg);
        }
      }
      &.position-up{
        img{
          .lh-rotate(270deg);
        }
      }

      &.xs{
        width: 15px;
        height: 15px;
      }
      &.sm{
        width: 25px;
        height: 25px;
      }
      &.md{
        width: 35px;
        height: 35px;
      }
      &.lg{
        width: 45px;
        height: 45px;
      }
    }


    // READ MORE JS
    .read-more.with-collapse{
      position: relative;
      min-height: 100px;
      overflow: hidden;

      .bottom-fade-out {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 3em;
        background: url('../images/white-gradient.png') bottom center repeat-x;
      }
    }
    a{
      &[data-readmore-toggle]{
        text-align: right;
        .color-lt-green;
        font-weight: 400;
        margin: 0.5em 0;

        &:visited{
          .color-lt-green;
        }
      }
    }

    // BOOTSTRAP PAGINATION
    .pagination-nav {
      text-align: center;
    }

    .pagination {
      .page-numbers {

        &.current {
          a {
            .color-white;
            .background-blue;
            cursor: default;
          }
        }

        a {
          .color-blue;
          font-weight: 600;
          .lh-border-radius(0) !important;
        }
      }
    }

    // BACK BUTTON
    .back-button{
      text-transform: uppercase;
      margin: 20px 0;
      width: 100%;
      max-width: 300px;
      white-space: normal;
    }

    // BREADCRUMB
    ol.breadcrumb{
      background: transparent;
      .lh-border-radius(0);
      padding: 15px 0 0;
      .font-weight(100, 1px);

      li{
        color: darken(@lt-grey, 15%);

        a{
          color: @lt-green;
        }
      }
    }

    //CF7
    .wpcf7-not-valid-tip{
      color: @green !important;
      .font-weight(100) !important;
    }

    .wpcf7-response-output{
      margin-top: 0 !important;
      margin-left: 0 !important;
      margin-right: 0 !important;
      padding: 5px !important;
      border: 0 !important;
      color: @white;
    }

    .wpcf7-validation-errors{
      background: @green !important;
    }

    .wpcf7-validation-success{
      background: @lt-green !important;
    }

    .wpcf7-mail-sent-ok{
      background: @lt-green !important;
    }
  }

  @media only screen and (min-width: @screen-sm-min) {
    .back-button{
      width: auto;
    }
  }

  @media only screen and (min-width: @screen-md-min) {

    // BREADCRUMB
    ol.breadcrumb{

      li{

        a{
          .hover-link(@blue);
        }
      }
    }

    // BOOTSTRAP PAGINATION
    .pagination {
      .page-numbers {

        a {
          .lh-transition(background .2s linear);
        }
      }
    }

    .btn{
      .shutter-out-vertical();
    }

    .the-subhead{
      .font-7;
    }
  }

  @media only screen and (min-width: @screen-lg-min) {
    article{
      .content{

      }
    }

  }
}

