body{
  &.blog{

    &.paged {
      .blog {
        .featured-post { display: none; }
      }
    }

    @media only screen and (min-width: 1px) {

      ul {
        padding-left: 0;

        li { list-style: none; }
      }

      .blog {
        position: relative;

        .row {
          border: 1px solid @ltst-grey;
          .horizontal-padding(15px);
          position: inherit;
          .vertical-margin;
          .vertical-padding(30px);

          &.featured-post {
            .background-dk-blue;
            .color-white;

            .title a { .color-white; }

            p { .font-weight(100, 0); }

            .video-container {
              .video-circle {
                .video-icon(@white, @dk-blue, @dk-blue);
              }
            }
          }

          .video-container {
            display: inline-block;
            position: relative;

            .video-circle {
              .video-icon(@dk-blue, @white, @white);
            }
          }

          .img-responsive { margin: 0; }

          .title {
            .color-blue;
            .font-3;
            .top-margin;
            .bottom-margin;
          }

          .chevron-container {
            position: absolute;
            bottom: 50%;
            right: 0;
            width: auto;

            .chevrons.position-right { margin-left: 0; }
          }
        }
      }
    }

    @media only screen and (min-width: @screen-sm-min) {
      .blog {
        .row {
          .title { margin-top: 0; }
          .img-responsive { margin: 0; }
        }
      }
    }

    @media only screen and (min-width: @screen-md-min) {

    }

    @media only screen and (min-width: @screen-lg-min) {
    }
  }

  &.single-blog {
    .back-button { margin-bottom: 20px; }
  }
}