/*OVERRIDES FOR DEFINED VARS IN BOWER_COMPONENTS */

// BOOTSTRAP
////////////

@icon-font-path:          "../../development/bower_components/bootstrap/fonts/";
@brand-primary: @blue;

// ICOMOON
//////////

@font-face {
  font-family: 'icomoon';
  src: url('../../development/bower_components/icomoon/dist/fonts/icomoon.eot?v=4.0.3');
  src: url('../../development/bower_components/icomoon/dist/fonts/icomoon.svg?v=4.0.3#icomoon') format('svg'), url('../../development/bower_components/icomoon/dist/fonts/icomoon.eot?#iefix&v=4.0.3') format('embedded-opentype'), url('../../development/bower_components/icomoon/dist/fonts/icomoon.woff?v=4.0.3') format('woff'), url('../../development/bower_components/icomoon/dist/fonts/icomoon.ttf?v=4.0.3') format('truetype');
  font-weight: normal;
  font-style: normal;
}
.icomoon{
  font-family: 'icomoon' !important;
}

// HOVER
////////

@primaryColor: @blue;
@secondaryColor: @dk-blue;
@highlightColor: lighten(@blue, 10%);
@activeColor: @lt-green;
@shadowColor: rgba(0, 0, 0, .6);
