body{
    &.page-template-template-testimonials {
        section.main-section {
            @media only screen and (min-width: 1px) {

              h2{
                &.logo-headline{
                  .color-blue;
                  font-size: 22px;
                  text-align: center;
                }
              }

              .testimonials{

                .testimonial{
                  border: @lt-grey solid 1px;
                  background: @white;
                  .vertical-padding;
                  margin: 0 0 15px;

                  .quotes{
                    text-align: left;
                    padding-bottom: 15px;

                    img{
                      .inline;
                      width: 50px;
                    }
                  }

                  .video{
                    padding-bottom: 15px;
                  }

                  &.featured{
                    background-color: @blue;
                    color: @white;
                  }

                  &:last-of-type{
                    margin-bottom: 0;
                  }
                }
              }
            }

            @media only screen and (min-width: @screen-sm-min) {
              .testimonials{
                .testimonial{

                  .quotes{

                    img{
                      width: 100%;
                    }
                  }

                  .text-container{
                    padding-left: 0;
                  }
                }
              }
            }

            @media only screen and (min-width: @screen-md-min) {
              .testimonials {
                .testimonial {

                }
              }
            }

            @media only screen and (min-width: @screen-lg-min) {

            }
        }
    }
}