section.main-section{

    body.single-training & {

      @media only screen and (min-width: 1px) {

        .videos{
          margin-bottom: 10px;
        }

        .video-item-wrap{

          .video-item{
            .padding;
            .vertical-margin(7.5px);
            border: 1px solid @ltst-grey;

            .image-container{
              text-align: center;
            }

            .title{
              .vertical-margin(7px);
              color: @blue;
              .font-weight(900);
            }

            .description{
              .font-size(12px, 18px);
            }
          }
        }
      }

      @media only screen and (min-width: @screen-sm-min) {

        .video-item-wrap {

          &.odd{
            padding-right: 7.5px;
          }
          &.even{
            padding-left: 7.5px;
          }

          .video-item {

            .info-container{
              padding-left: 0;
            }

            .title {

            }

            .description {
              height: 125px;
            }
          }
        }
      }

      @media only screen and (min-width: @screen-md-min) {

        .video-item-wrap {

          &.odd{
            padding-right: 15px;
          }
          &.even{
            padding-left: 15px;
          }

          .video-item {

            .info-container{

            }

            .title {
              cursor: pointer;
              .hover-link(@lt-green);
            }

            .description {
              height: 60px;
            }
          }
        }
      }

      @media only screen and (min-width: @screen-lg-min) {

        .video-item-wrap {

          &.odd{
            padding-right: 7.5px;
          }
          &.even{
            padding-left: 7.5px;
          }

          .video-item {

            .info-container{

            }

            .title {

            }

            .description {
              height: 117px;
            }
          }
        }

      }
    }
}