body {
  &.contact {
    @media only screen and (min-width: 1px){
      .contact-info{
        text-align: center;
      }

      .infusionsoft-form{
        margin-bottom: 15px;

        .form-container{
          padding: 0 15px 15px 15px;
          background: @lt-green;
          margin-top: 15px;

          .infusion-field-input-container{
            background: @white;
            border-color: @ltst-grey;
          }
        }
      }
    }

    @media only screen and (min-width: @screen-sm-min) {
    }

    @media only screen and (min-width: @screen-md-min) {
      .contact-info{
        margin-bottom: 20px;
      }
      .infusionsoft-form {
        margin-bottom: 30px;

        .form-container{

          .infusion-submit{
            width: 300px;
            margin: 0 auto;
          }
        }
      }
    }

    @media only screen and (min-width: @screen-lg-min) {

    }
  }
}