.component.modal {

    body & {
        @media only screen and (min-width: 1px) {

          z-index: 9999;

          *{
            .lh-border-radius(0);
          }

          .modal-header{
            background: @lt-green;
            color: @white;

            .modal-title{
              .roboto-slab;
              text-transform: uppercase;
            }
          }

          iframe.vimeo{
            height: 200px;
          }
        }

        @media only screen and (min-width: @screen-sm-min) {

          iframe.vimeo{
            height: 320px;
          }
        }

        @media only screen and (min-width: @screen-md-min) {

          .modal-dialog{
            margin-top: 60px;
          }
        }

        @media only screen and (min-width: @screen-lg-min) {
        }
    }
}