body.single-team {

    @media only screen and (min-width: 1px) {

      .main-section{

        .bio-container{
          padding-bottom: 15px;

          .title-container{

            h1{
              margin-bottom: 0;
            }

            .member-position{
              .font-2;
              color: @lt-green;
              text-transform: uppercase;
              .font-weight(100, 1px);
            }
          }

          .member-thumb{
            text-align: center;
            margin-bottom: 15px;

            .wrap {
              .inline(middle);
              max-width: 300px;
              border: 1px solid @lt-grey;
              padding: 10px;

              img {
                display: block;
              }
            }

            .thumb-title, .thumb-position {
              display: none;
            }

            .thumb-title {

            }

            .thumb-position {

            }
          }
        }
      }

    }

    @media only screen and (min-width: @screen-sm-min) {

      .main-section{

        .bio-container{

          .member-thumb{
            .pull-right;
            margin: 0 0 15px 15px;

            .thumb-title, .thumb-position {
              display: block;
              text-align: center;
              color: darken(@lt-grey,15%);
            }

            .thumb-title {
              margin-top: 5px;
            }

            .thumb-position {
              .font-size(12px, 16px);
              text-transform: uppercase;
              .font-weight(100, 1px);
            }
          }
        }
      }
    }

    @media only screen and (min-width: @screen-md-min) {

    }

    @media only screen and (min-width: @screen-lg-min) {

    }
}