body{

  &.news,
  &.releases{

    @media only screen and (min-width: 1px) {

      ul {
        padding-left: 0;

        li { list-style: none; }
      }

      .news, .press {

        .row {
          border-bottom: 1px solid @ltst-grey;
          position: relative;

          .thumb {
            .inline;
            text-decoration: none;
            position: relative;

            .video-circle {
              .video-icon(@dk-blue, @white, @white);
            }
          }

          .img-responsive { margin: 0; }

          .title {
            color: @blue;
            .font-3;
            .vertical-margin;
          }

          .chevron-container{

            a{

              .chevrons{
                .inline(middle);
              }
            }
          }

          &.featured-post {
            background: @blue;
            color: @white;

            .title a {
              color: @white;
            }

            p { .font-weight(100, 0); }

            .thumb {
              .video-circle {
                .video-icon(@white, @dk-blue, @dk-blue);
              }
            }

            .chevron-container{

              a{
                color: @white;
              }
            }
          }
        }
      }
    }

    @media only screen and (min-width: @screen-sm-min) {
      .news, .press {
        .row {
          .title { margin-top: 0; }
          .img-responsive { margin: 0; }
        }
      }
    }

    @media only screen and (min-width: @screen-md-min) {

      .news, .press {
        position: relative;

        .row {
          border: 1px solid @ltst-grey;
          position: inherit;
          margin-bottom: 15px;

          .thumb{
            border: 2px solid transparent;
            .lh-transition(border .2s linear);

            &:hover{
              border-color: @lt-green;
            }
          }

          .title {

            a{
              .hover-link(@lt-green);
            }
          }

          .chevron-container {
            position: absolute;
            bottom: 50%;
            right: 0;
            width: auto;

            a{
              .wobble-horizontal();

              .chevrons{
                width: 35px;
                height: auto;

                img{
                  height: 35px;
                }
              }
            }
          }
        }
      }
    }

    @media only screen and (min-width: @screen-lg-min) {
    }
  }

  &.single-news_press {
    .back-button { margin-bottom: 20px; }
  }
}