body.single-course {

  @media only screen and (min-width: 1px) {

    article{
        padding-top: 0;

        h1{
          margin-top: 15px;
        }
      }
      .seminar-section {
        .top-margin;

        .seminars {
          padding-left: 0;

          .seminar {
            border-top: 1px solid @lt-grey;
            list-style: none;
            .top-padding;
            .top-margin;

            .row {
              padding-bottom: 5px;

              .link {
                margin-bottom: 5px;
                text-align: right;
              }

              .seminar-title {
                .font-3;
              }

              .date{
                .inline;
                margin-top: 5px;
                .font-weight(100);

                .glyphicon{
                  .font-size(20px,1);
                  color: @lt-green;
                }

                .text{
                  .inline;
                  margin-top: 1.5px;
                }
              }

              &.content {
                margin-top: 5px;
              }
            }
          }
        }
      }
  }

  @media only screen and (min-width: @screen-sm-min) {

  }

  @media only screen and (min-width: @screen-md-min) {

    article{

        h1{

        }
      }
      .seminar-section {

        .seminars {

          .seminar {

            .row {

              .link {
                a {
                  .hover-link(@lt-green);
                }
              }

              .seminar-title {

                a{
                  .hover-link(@lt-green);
                }
              }

              .date{

                .glyphicon{

                }

                .text{

                }
              }

              &.content {

              }
            }
          }
        }
      }
  }

  @media only screen and (min-width: @screen-lg-min) {

  }
}