body{
    &.books-resources,
    &.page-template-template-books-resources {
        section.main-section {
            @media only screen and (min-width: 1px){
                #books-resources-results {
                    .row {
                        padding-bottom: 25px;
                        border-bottom: 1px solid @lt-grey;
                        margin-bottom: 25px;

                        &:last-of-type {
                            border-bottom: none;
                            padding-bottom: 0;
                        }

                        .image {
                            text-align: center;

                            img {
                                max-width: 100%;
                            }
                        }

                        .title-description {
                            .title {
                                padding-top: 20px;
                                .color-blue;
                                font-size: 1.25em;
                                font-weight: 600;
                            }
                            .description{
                              max-height: 100px;
                            }
                        }

                        .price-links {
                            text-align: right;

                            .price {
                                .color-lt-green;
                                font-size: 1.5em;
                                font-weight: 400;
                                margin-bottom: 0.5em;
                            }
                        }
                    }
                }
            }

            @media only screen and (min-width: @screen-sm-min) {
                #books-resources-results {
                    .row {
                        .title-description {
                            .title {
                                padding-top: 0;
                            }
                        }
                    }
                }
            }

            @media only screen and (min-width: @screen-md-min) {
            }

            @media only screen and (min-width: @screen-lg-min) {

            }
        }
    }
}