section.main-section {

  body.team & {

    @media only screen and (min-width: 1px) {
      ul {
        padding-left: 0;
        list-style: none;
        margin: 0;

        .team-member {
          list-style: none;
          margin-bottom: 15px;

          > .row {
            border-bottom: 1px solid @lt-grey;
            .vertical-padding(15px);
          }

          .thumb-container{
            text-align: center;
            padding-bottom: 15px;

            a{
              .inline(middle);
              line-height: 0;
              max-width: 200px;

              img{
                width: 100%;
              }
            }
          }

          .name-container {
            text-align: center;

            .member-name {
              .font-3;

              a {
                text-decoration: none !important;
              }
            }

            .member-position{
              .font-weight(100, 1px);
              text-transform: uppercase;
              color: @lt-green;
            }

          }
        }
      }
    }

    @media only screen and (min-width: @screen-sm-min) {

      ul {

        .team-member {

          > .row {

          }

          .thumb-container {
            text-align: left;

            a {

            }
          }

          .name-container {
            text-align: left;
          }
        }
      }
    }

    @media only screen and (min-width: @screen-md-min) {

      ul {

        .team-member {
          border: 1px solid @lt-grey;
          .horizontal-padding;

          > .row {
            border: 0;
          }

          .thumb-container {

            a {
              border: 2px solid transparent;
              .lh-transition(border .2s linear);

              &:hover{
                border-color: @lt-green;
              }
            }
          }

          .info-wrap.with-thumb{
            padding-left: 0;
          }

          .name-container {

            .member-name{

              a {
                .hover-link(lighten(@blue, 10%));
              }
            }
          }
        }
      }
    }

    @media only screen and (min-width: @screen-lg-min) {

      ul {

        .team-member {

          .read-more-container{
            padding-top: 5px;
          }
        }
      }
    }
  }

}