.royalSlider{
  &.rsDefault {
    @media only screen and (min-width: 1px) {
      background: @white !important;
      margin-bottom: 20px;

      .rsOverflow,
      .rsSlide,
      .rsVideoFrameHolder,
      .rsThumbs{
        background: @white !important;
      }

      .rsArrow{
        width: 60px;
      }

      .rsArrowLeft{
        .rsArrowIcn{
          .lh-transform(rotate(180deg));
        }
      }
      .rsArrowIcn{
        .lh-transition(background-color .75s ease);
        background-color: @lt-green;
        background-image: url('../images/chevrons/chevron-white.png');
        background-size: cover;
        background-size: 102% 80%;
        background-position: 0 !important;
        background-repeat: no-repeat;

        width: 42px;
        height: 42px;
        margin-top: -21px;
        margin-left: -21px;

        &:hover{
          .background-green;
        }
      }

      .rsBullets{
        background: @white !important;
        bottom: -20px;

        .rsBullet{
          span{
            background: @lt-green;
            width: 12px;
            height: 12px;
          }
          &.rsNavSelected{
            span{
              background: @green;
            }
          }
        }
      }
    }

    @media only screen and (min-width: @screen-sm-min) {

    }

    @media only screen and (min-width: @screen-md-min) {

    }

    @media only screen and (min-width: @screen-lg-min) {

    }
  }
}