.component.masthead {

    body & {
        @media only screen and (min-width: 1px) {
          overflow: hidden;
          text-align: left;
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          position: relative;
          background-color: @ltst-grey;

          #particle{
            display: none;
            position: absolute;
            width: 150px;
            height: 150px;
            margin-left: -75px;
            margin-top: -75px;
            z-index: 9;
            opacity: 0.4;
            background: url('../images/white-particle-effect.gif') top left no-repeat;
            background-size: 100%;
          }

          .text-container{
            width: 100%;
            min-height: 120px;
            padding: 0;
            margin: 0;

            .masthead-text{
              .font-4;
              color: @white;
              .font-weight(400);
              .vertical-padding;
              .lh-drop-shadow(0 0 5px rgba(0,0,0,0.7));
            }
          }

        }

        @media only screen and (min-width: @screen-sm-min) {
          #particle {
            opacity: 0.75;
          }

          .text-container {
            min-height: 150px;

            .masthead-text {
              .font-6;
            }
          }

        }

        @media only screen and (min-width: @screen-md-min) {

          #particle {
            width: 200px;
            height: 200px;
            margin-left: -100px;
            margin-top: -100px;
          }

          padding-top: 40px;
          margin-top: -41px;

          .text-container {
            min-height: 200px;

            .masthead-text {
              .font-10;
              .font-weight(700);
            }
          }
        }

        @media only screen and (min-width: @screen-lg-min) {


        }
    }

    // HOME
    body.home & {

      @media only screen and (min-width: 1px) {

        text-align: center;

        .text-container{
          min-height: 180px;
        }
      }

      @media only screen and (min-width: @screen-sm-min) {

        .text-container{
          min-height: 200px;
        }
      }

      @media only screen and (min-width: @screen-md-min) {

        .text-container{
          min-height: 320px;
        }
      }

      @media only screen and (min-width: @screen-lg-min) {
      }
    }


  // X BROWSER /////////////////////////////////////////////////
  /////////////////////////////////////////////////////////////

  html.firefox & {

    @media only screen and (min-width: @screen-md-min) {

      .text-container{
        height: 200px;
      }
    }

  }

  html.firefox .home & {

    @media only screen and (min-width: @screen-md-min) {

      .text-container{
        height: 320px;
      }
    }

  }
}

