#sidebar-promotion{
    @media only screen and (min-width: 1px) {
        width: 100%;
        padding: 20px;
        background: @lt-green;

        .promo{
          text-align: center;

          h3{
            .color-white;
            font-size: 25px;
            line-height: 30px;
            font-style: italic;
          }

          .image{
            margin-top: 10px;

            img{
              max-width: 80%;
            }
          }

          .content{
            margin: 15px 0;

            p{
              text-align: center;
              font-weight: 600;
            }
          }

          .btn-container{
            .btn {
              padding: 10px 15px 10px 25px;

              .btn-text {
                .font-3;
                text-transform: uppercase;
                position: relative;
                top: 3px;
              }
            }
          }
        }
    }

    @media only screen and (min-width: @screen-sm-min) {
    }

    @media only screen and (min-width: @screen-md-min) {
      padding: 8px;
      .lh-background-image(linear-gradient(to right, @lt-grey 0%, @white 50%, @lt-grey 100%));
      margin-bottom: 15px;

      .promo{
        padding: 20px;
        background: @lt-green;
      }
    }

    @media only screen and (min-width: @screen-lg-min) {
    }
}