body{
  &.page-template-template-upcoming-seminars {
    section.main-section {
      @media only screen and (min-width: 1px){

        .seminars{
          list-style: none;
          margin: 0;
          padding: 0;

          .seminar{
            margin-bottom: 30px;
            padding-bottom: 30px;
            border-bottom: @lt-green solid 1px;

            &:last-of-type{
              padding-bottom: 0;
              border-bottom: none;
            }

            .title-date{
              .title{

              }
              .date{
                .inline;
                margin-top: 5px;
                .font-weight(100);

                .glyphicon{
                  .font-size(20px,1);
                  color: @lt-green;
                }

                .text{
                  .inline;
                  margin-top: 1.5px;
                }
              }
            }

            .links{
              text-align: right;

              a{
                color: @blue;
              }
            }

            .content{
              margin: 15px 0 0 0;
            }
          }
        }
      }

      @media only screen and (min-width: @screen-sm-min) {

      }

      @media only screen and (min-width: @screen-md-min) {

        .seminars{

          .seminar{

            .title-date{
              .title{

                a{
                  .hover-link(@lt-green);
                }
              }
              .date{

                span{

                }
              }
            }

            .links{

              a{
                .hover-link(@lt-green);
              }
            }

            .content{
              margin: 15px 0 0 0;
            }
          }
        }
      }

      @media only screen and (min-width: @screen-lg-min) {

      }
    }
  }
}