html, body {
    background: @ltst-grey;
}

body {
    .roboto;
    position: relative;
}

header{

    body & {
        @media only screen and (min-width: 1px) {

            .header-logo-container {
                text-align: center;
                .vertical-padding;

                .header-logo {
                    .inline(middle);
                    width: 130px;
                }
            }

            .header-nav-outer {
                background: @dk-blue;


            }

            .primary-nav-outer {
                background: @ltst-grey;

                .primary-nav-container {
                    .horizontal-padding(0);

                    #primary-nav {

                        .ubermenu-responsive-toggle{
                            background: darken(@ltst-grey,5%);
                        }

                        .ubermenu-column, .ubermenu-column-auto{
                            min-width: 100% !important;
                        }

                        .ubermenu-main {
                            background: @ltst-grey;
                            color: @dk-blue;

                            .top-nav-item a {
                                background: @dk-blue;
                                color: @white;
                            }

                            .shopping-cart-icon {
                                background: @dk-blue;

                                a {
                                    display: block;

                                    img {
                                        width: 30px;
                                    }
                                }
                            }

                            .custom-box{

                                .ubermenu-custom-content{
                                    padding: 0 !important;

                                    .title{
                                      font-size: 17px;
                                      margin-bottom: 10px;
                                    }
                                }

                                a{
                                    background: @lt-green;
                                    .roboto-slab;
                                    display: block;
                                    .padding(15px 20px);

                                    .title{
                                        .font-1;
                                        color: @white;
                                    }

                                    .desc{
                                        color: @dk-blue;
                                    }
                                }
                            }

                            .ubermenu-retractor{
                                background: darken(@blue,5%);
                                color: @white;
                            }
                        }
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-sm-min) {
        }

        @media only screen and (min-width: @screen-md-min) {

            z-index: 9999;
            width: 100%;
            position: absolute;
            .lh-box-shadow(0 0 2px rgba(0,0,0,0.1));

            .primary-wrap{
              position: relative;
            }

            .header-logo-container {
                position: absolute;
                left: 0;
                top: 0;
                z-index: 1;
                text-align: left;
                padding-bottom: 0;

                .header-logo {
                    width: 155px;
                    height: 60px;

                    img{
                        width: auto;
                        height: 100%;
                        .lh-transition(all .2s ease-in-out);

                        &:hover{
                            .lh-scale(1.1);
                        }
                    }
                }
            }

            .header-nav-outer {

                #header-nav {
                    a {
                        padding-top: 10px;
                        padding-bottom: 10px;
                        .lh-transition(background .2s linear);
                    }

                    .shopping-cart-icon {

                        a {
                            display: block;
                            padding-left: 10px;
                            padding-right: 10px;

                            img {
                                width: 20px;
                                display: block;
                            }

                            &:hover{

                                img{
                                    .lh-animation(swing .5s ease-in-out);
                                }
                            }
                        }
                    }
                }
            }

            .primary-nav-outer {

                .primary-nav-container {
                    .horizontal-padding(15px);

                    #primary-nav {
                        z-index: 10;

                        .ubermenu.ubermenu-responsive .ubermenu-column, .ubermenu.ubermenu-responsive .ubermenu-column-auto {
                            min-width: initial !important;
                        }

                        .ubermenu-main {
                            background: transparent;
                            margin: 0;
                            .clearfix;

                            .ubermenu-target[tabindex="0"]{
                                padding-top: 35px;
                                padding-bottom: 35px;
                                .lh-transition(background .2s linear);

                                &:hover{
                                    background: lighten(@ltst-grey,2%);
                                }
                            }

                            .main-item span, .main-item a{
                              .font-1;
                            }

                            .ubermenu-has-submenu-drop{

                              .ubermenu-target[tabindex="0"] {
                                position: relative;
                                overflow: hidden;

                                &:before {
                                  content: '';
                                  width: 0;
                                  height: 0;
                                  border-left: 15px solid transparent;
                                  border-right: 15px solid transparent;
                                  border-bottom: 15px solid @blue;
                                  position: absolute;
                                  bottom: -20%;
                                  left: 38%;
                                  .lh-opacity(0);
                                  .lh-transition(bottom .2s ease-in .1s, opacity .2s linear);
                                }
                              }

                              &.ubermenu-active{

                                .ubermenu-target[tabindex="0"] {

                                  &:before {
                                    .lh-opacity(1);
                                    bottom: 0;
                                  }
                                }
                              }

                              > .ubermenu-submenu{
                                border: 0 !important;
                                background-color: transparent;
                                .lh-background-image(linear-gradient(to bottom, rgba(red(@blue), green(@blue), blue(@blue), 1.0) 0%, rgba(red(@blue), green(@blue), blue(@blue), 0.8) 100%));
                              }
                            }

                            .ubermenu-submenu{

                              .ubermenu-item{
                                border-left: 1px solid #2f6472;

                                &:first-of-type{
                                  border: 0;
                                }
                              }

                              .menu-title{
                                .roboto-slab;
                                .font-2;
                                text-align: center;

                                .ubermenu-submenu{
                                  .roboto;
                                  .font-size(12px, 1.3em);

                                  .ubermenu-item{
                                    text-align: center;
                                    font-size: 13px;
                                  }
                                }

                                a{
                                  .hover-link(lighten(@lt-green,15%));
                                  .font-2;
                                }
                              }

                              .service-link{

                                a{
                                  .hover-link(lighten(@lt-green,15%))
                                }
                              }
                            }

                            .custom-box{
                              .pull-right;
                              border: 0;

                                a {
                                  text-align: center;
                                  .lh-transition(background .2s linear);
                                  position: absolute;
                                  right: 0;
                                  top: 0;
                                  height: 100%;
                                  width: 200px;

                                    table {
                                        height: 100%;

                                        .title {

                                        }

                                        .desc {

                                        }
                                    }

                                    &:hover{
                                        background: lighten(@lt-green,5%);
                                        text-decoration: none !important;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-lg-min) {

            .header-logo-container {

                .header-logo {
                    width: 150px;
                }
            }

            .header-nav-outer {

                #header-nav {

                    a {

                    }
                }
            }

            .primary-nav-outer {

                .container {

                    #primary-nav {

                    }
                }
            }
        }
    }
}

section.main-section{

    body & {
        @media only screen and (min-width: 1px) {

            background: @white;

            article{
                padding-top: 20px;

                .breadcrumb-container{
                    .breadcrumb{
                        margin-bottom: 0;
                    }
                }
              h1{
                &:first-of-type{
                  margin-bottom: 1em;
                }
              }
            }

            aside{
                padding-top: 20px;
            }

        }

        @media only screen and (min-width: @screen-sm-min) {
        }

        @media only screen and (min-width: @screen-md-min) {

          padding-top: 122px;
        }

        @media only screen and (min-width: @screen-lg-min) {


        }
    }
}

//STICKY HEADER
body.sticky-header {

    @media only screen and (min-width: @screen-md-min) {

        header {
            position: fixed;

            .header-logo-container {
                padding-top: 5px;
                padding-bottom: 5px;

                .header-logo {
                  width: 100px;
                  height: 40px;

                    .logo-normal {
                      width: 80%;
                    }
                }
            }

            .header-nav-outer {
                display: none;
            }

            .primary-nav-outer {

                .primary-nav-container {

                    #primary-nav {

                        .ubermenu-main {

                            .ubermenu-target[tabindex="0"] {
                                padding-top: 15px;
                                padding-bottom: 15px;
                            }
                        }
                    }
                }
            }
        }
    }
}

//ANIMATION FOR STICKY HEADER
body.sticky-animation{

    @media only screen and (min-width: @screen-md-min) {

        .header-logo-container {

            .header-logo {
                .lh-transition(all .2s ease-in);
            }
        }

        .primary-nav-outer {

            .primary-nav-container {

                #primary-nav {

                    .ubermenu-main {

                        .ubermenu-target[tabindex="0"] {
                            .lh-transition(padding .2s ease-in);
                        }
                    }
                }
            }
        }
    }
}

footer{

    body & {
        @media only screen and (min-width: 1px) {

            background: @ltst-grey;
            .font-size(12px,16px);

            .container{
                .vertical-padding;
                .roboto-slab;

                h3{
                    margin-top: 0;
                    color: @blue;
                    .font-3;
                }

                .link-list{
                    list-style: none;
                    margin: 0;
                    padding: 0;

                    li{

                        a{
                            display: block;
                            .padding(5px 0);
                            color: @dk-blue;
                        }
                    }
                }

                .industries{
                    padding-bottom: 15px;
                }

                .page-links{
                    .font-weight(bold);
                    border-top: 1px solid @white;
                    padding-top: 15px;
                }

                .contact-form{
                    padding-top: 30px;
                    text-align: center;

                    h2{
                        margin: 0;
                        .font-weight(bold);
                        color: @lt-green;
                        .font-4;
                    }

                    .title{
                        .font-2;
                        color: @blue;
                        .font-weight(bold);
                        margin-bottom: 15px;

                        a{
                            .phone-link(@blue);
                        }
                    }

                    .newsletter-form{
                        text-align: left;
                        padding: 15px 0;

                        .form-control{
                            background: @lt-grey;
                            border-color: @white;
                            .form-control-focus(@lt-green);
                        }

                        .btn{
                            width: 100%;
                        }

                        .ajax-loader{
                            margin: 0;
                            padding-top: 3px;
                        }
                    }
                }

                .footer-address-container{
                    text-align: center;

                    .footer-address{
                        color: @grey;
                        padding-bottom: 5px;
                        margin-bottom: 5px;
                        border-bottom: 1px solid @white;
                    }
                }

              .zz-link{

                text-align: center;
                color: @lt-green;

                a{
                  color: @lt-green;
                }
              }
            }
        }

        @media only screen and (min-width: @screen-sm-min) {

            .container{

                .page-links{
                    padding-top: 0;
                    border-top: 0;
                    .clearfix;

                    .page-list{
                        .pull-left;
                        width: 50%;
                    }
                }
            }
        }

        @media only screen and (min-width: @screen-md-min) {

            .container{

                .industries{
                    padding-bottom: 0;

                  .link-list{

                    a{
                      .hover-link(@lt-green);
                    }
                  }
                }

                .page-links{

                  a{
                    .hover-link( lighten(@blue,10%) );
                  }
                }

                .contact-form{
                    padding-top: 0;

                    .newsletter-form{

                        .btn{

                        }
                    }
                }

                .footer-address-container{
                    text-align: left;
                    padding-top: 15px;
                    margin-bottom: 0;
                    border-top: 1px solid @white;

                    .footer-address{
                        padding-bottom: 0;
                        margin-bottom: 0;
                        border-bottom: 0;
                        .inline;
                    }

                    .privacy-policy{
                        .inline;
                        margin-left: 5px;
                    }
                }

                .zz-link{
                  text-align: right;

                  a{
                    .hover-link(@green);
                  }
                }
            }
        }

        @media only screen and (min-width: @screen-lg-min) {


        }
    }
}


// X BROWSER /////////////////////////////////////////////////
/////////////////////////////////////////////////////////////

html.mozilla-5, html.internet-explorer{

    body header .primary-nav-outer .primary-nav-container #primary-nav .ubermenu.ubermenu-responsive .ubermenu-column, body header .primary-nav-outer .primary-nav-container #primary-nav .ubermenu.ubermenu-responsive .ubermenu-column-auto{
      min-width: 0 !important;
    }

    body header .primary-nav-outer .primary-nav-container #primary-nav .ubermenu-main .custom-box a .desc{
      font-size: 14px;
      line-height: 16px;
    }

}