body {
  &.single-seminar {

    @media only screen and (min-width: 1px){
      .info{
        .date-title{
          .color-blue;

          .title{
            text-align: left;
            margin: 0;
          }
          .date{
            .inline;
            margin-top: 5px;
            .font-weight(100);

            .glyphicon{
              .font-size(20px,1);
              color: @lt-green;
            }

            .text{
              .inline;
              margin-top: 1.5px;
            }
          }
        }
        .location{
          margin-top: 15px;

          .name{
            font-size: 1.25em;
          }
          .city-state{

          }
        }
      }

      .content{
        margin: 20px 0;
      }

      .instructors{
        margin-top: 20px;
        padding-top: 20px;
        overflow: hidden;
        
        h3{
          margin-bottom: 15px;
        }

        .instructor{
          padding-bottom: 20px;
          margin-bottom: 20px;
          border-bottom: 1px solid @ltst-grey;

          .thumb-container {
            margin-bottom: 10px;

            .thumb {
              .inline;
              width: 100%;
              max-width: 200px;

              img{
                width: 100%;
                max-width: 200px;
              }
            }
          }

          &:last-of-type{
            margin-bottom: 0;
            padding-bottom: 0;
            border-bottom: none;
          }

          .name{
            .font-3;
            font-weight: 600;
            margin-bottom: 5px;
          }
        }
      }

      .event-location-container{
        margin-top: 20px;
        padding-top: 20px;

        .time-location{
          margin: 10px 0;
        }

        .map{
          text-align: center;
          padding: 20px;
          margin: 0 -15px;
          .background-ltst-grey;

          a{
            &:hover{
              .color-white;
            }

            img{
              border: 2px solid @grey;
            }

            div{
              text-decoration: none;
              font-size: 12px;
              .color-white;
              .background-grey;
              padding: 3px 0;
              max-width: 404px;
              margin: 0 auto;
            }
          }
        }
      }

      .infusionsoft-form{
        margin-top: 20px;
      }
    }

    @media only screen and (min-width: @screen-sm-min) {
      .info {
        .location {
          text-align: right;
          margin-top: 0;
        }
      }

      .instructors{

        .instructor{

          .thumb-container{

            .thumb {

              img{

              }

              .icomoon{
                color: @grey;
                .inline(middle);
                padding: 25px 0;
                .font-size(150px,1);
              }
            }
          }
        }
      }

      .infusionsoft-form{
        margin-top: 0;
      }
    }

    @media only screen and (min-width: @screen-md-min) {
      .instructors{
        .instructor{
          .bio{
            .read-more {
              max-height: 200px;
            }
          }
        }
      }

      .event-location-container{
        .map{
          margin: 0;
        }
      }
    }

    @media only screen and (min-width: @screen-lg-min) {

    }
  }
}