body {
  &.page-template-template-course-catalog {

    @media only screen and (min-width: 1px){
      .content{
        margin-bottom: 25px;
      }

      .courses {
        padding-left: 0;

        .course {
          border-top: 1px solid @lt-grey;
          list-style: none;
          .vertical-padding;
        }

        .title-container{
          .vertical-padding;
        }

        .course-title {
          color: @blue;
          .font-3;
          margin: 0;
        }

        .links,
        .mobile-details{
          text-align: right;
        }
      }
    }

    @media only screen and (min-width: @screen-sm-min) {

    }

    @media only screen and (min-width: @screen-md-min) {

      .courses {

        .course-title {
          color: @blue;
          .font-3;
          margin: 0;

          a{
            .hover-link(@lt-green);
          }
        }

        .links,
        .mobile-details{
          text-align: right;

          a{
            .hover-link(@lt-green);
          }
        }
      }
    }

    @media only screen and (min-width: @screen-lg-min) {
      article {
        .content {

        }
      }
    }
  }
}
