body{
  .component.big-list {
    @media only screen and (min-width: 1px) {

      .vertical-padding;

      .list-title {
        margin-top: 0;
        margin-bottom: 15px;
        text-align: center;
      }

      ul {
        list-style: none;
        margin: 0 0 20px;
        padding: 0;
        text-align: center;

        li {
          .inline(middle);

          .item-content {
            width: 200px;
            height: 200px;
            overflow: hidden;
            .lh-border-radius(500px);
            color: @white;
            position: relative;
            margin-bottom: -25px;
            .roboto-slab;

            td {
              .horizontal-padding(15px);
              .font-3;

              .big {
                .font-10;
                display: block;
              }

              .upper {
                text-transform: uppercase;
              }
            }

            &.item-1 {
              .rgb-bg(@blue, 0.8);
              z-index: 1;
            }
            &.item-2 {
              .rgb-bg(@lt-green, 0.8);
              z-index: 2;
              right: -15px;
            }
            &.item-3 {
              .rgb-bg(@green, 0.8);
              z-index: 3;
            }
            &.item-4 {
              .rgb-bg(@blue, 0.8);
              z-index: 4;
              right: -15px;
            }
            &.item-5 {
              .rgb-bg(@dk-blue, 0.8);
              z-index: 5;
            }
          }
        }
      }
    }

    @media only screen and (min-width: 434px) {

      .list-title {

      }

      ul {

        li {

          .item-content {

            td {

              .big {

              }

              .upper {

              }
            }

            &.item-1 {

            }
            &.item-2 {
              right: 0;
            }
            &.item-3 {

            }
            &.item-4 {
              right: 0;
            }
            &.item-5 {

            }
          }
        }
      }
    }

    @media only screen and (min-width: @screen-sm-min) {

      .vertical-padding(30px);
    }

    @media only screen and (min-width: @screen-md-min) {
      border-top: 1px solid @lt-grey;
      border-bottom: 1px solid @lt-grey;

      .list-title {
        .font-6;
        margin-bottom: 45px;
      }

      ul {
        margin: 0 0 30px 20px;

        li {

          .item-content {
            margin-bottom: 0;
            margin-left: -20px;

            td {

              .big {

              }

              .upper {

              }
            }

            &.item-1 {

            }
            &.item-2 {
              bottom: -20px;
            }
            &.item-3 {

            }
            &.item-4 {
              bottom: -20px;
            }
            &.item-5 {

            }
          }
        }
      }
    }

    @media only screen and (min-width: @screen-lg-min) {

      .list-title {

      }

      ul {
        margin: 0 0 30px 25px;

        li {

          .item-content {
            width: 250px;
            height: 250px;
            margin-left: -30px;

            td {
              .font-4;

              .big {
                .font-12;
              }

              .upper {

              }
            }

            &.item-1 {

            }
            &.item-2 {
              bottom: -30px;
            }
            &.item-3 {

            }
            &.item-4 {
              bottom: -30px;
            }
            &.item-5 {

            }
          }
        }
      }
    }
  }

  &.home {
    .component.big-list{
      border-top: none;
      border-bottom: none;
    }
  }
}