#sidebar {
  @media only screen and (min-width: 1px) {
    .background-lt-green;
    padding: 20px;
    margin-bottom: 20px;

    h3{
      font-size: 18px;
      line-height: 1.25em;
      margin: 0 0 10px 0;
      .color-white;
    }

    select {
      display:none;
      .roboto-slab;
      .color-white;
      font-weight: 400;
      .font-1;
      width: 100%;
      outline: none !important;
      .lh-appearance(none);
      border: none;
      background: #95d759 url('../images/chevrons/chevron-dropdown-bg.png') 95% center no-repeat;
      background-size: 20px 15px;
      padding: 5px 5px 5px 15px;
      .lh-border-radius(0);
    }
  }

  @media only screen and (min-width: @screen-sm-min) {

    ul{

      li{
        padding: 3.5px 0;
      }
    }
  }

  @media only screen and (min-width: @screen-md-min) {
    .background-transparent;
    padding: 0 20px;

    h3{
      margin: 0 -20px;
      .background-blue;
      padding: 5px 10px 5px 40px;
      font-size: 16px;
      .font-4;
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 10px 0 10px 20px;

      li {
        color: @blue;
        .font-3;
        position: relative;

        a{
          color: @blue;
          display: block;
          .lh-transition(all 0.2s ease);

          &:hover{
            color: lighten(@blue, 10%);
            text-decoration: none;
            margin-left: -15px;
          }
        }

        .chevrons {
          position: absolute;
          left: -40px;
          top: 8px;
        }
      }
    }
  }

  @media only screen and (min-width: @screen-lg-min) {
  }
}