.component.testimonials {

    body & {
        @media only screen and (min-width: 1px) {

          background-color: @ltst-grey;
          background-position: left center;
          background-size: cover;
          background-repeat: no-repeat;

          .container {
            .vertical-padding;
            .rgb-bg(@ltst-grey, 0.8);

            .quote-icon{

              img{
                width: 60px;
                margin-bottom: 15px;
              }
            }

            .item {
              padding: 0;
              margin: 0;
              height: 0;
              position: relative;

              .content, .name, .company{
                position: absolute;
                height: 0;
                .inline(middle);
                overflow: hidden;
              }

              &.active {
                height: 300px;
                overflow: hidden;

                .content, .name, .company{
                  position: relative;
                  width: 100%;
                  height: auto;
                  overflow: initial;
                  .lh-animation(fadeInRight 1s ease-in-out);
                }

                .content{
                  .font-size(16px, 22px);
                  margin-bottom: 15px;
                }

                .name{
                  .font-weight(900);
                }

                .name, .company{
                  .font-3;
                }
              }

              &.hiding{

                .content, .name, .company {
                  .animated;
                  .fadeOut;
                }
              }
            }
          }
        }

        @media only screen and (min-width: 600px) {

          .container {

            .quote-icon{

              img{
                width: 80px;
              }
            }

            .item {

              &.active {
                height: 200px;
              }
            }
          }
        }

        @media only screen and (min-width: @screen-sm-min) {

          .container {
            .rgb-bg(@ltst-grey, 0.6);

            .quote-icon{
              text-align: right;
            }

            .item {

              &.active {
                height: 200px;
              }
            }
          }
        }

        @media only screen and (min-width: @screen-md-min) {

          overflow: hidden;

          .container {
            background: transparent;
            .vertical-padding(60px);

            .item {

              &.active {
                height: 230px;
                overflow: inherit;
              }
            }
          }
        }

        @media only screen and (min-width: @screen-lg-min) {

          .container {
            .vertical-padding(80px);

            .quote-icon{

              img{
                width: 90px;
              }
            }

            .item {

              &.active {
                height: 300px;

                .content, .name, .company{
                  .font-size(18px, 32px);
                }
              }
            }
          }
        }
    }

}