.infusion-form {
  @media only screen and (min-width: 1px) {
    text-align: left;
    padding-top: 15px;

    .infusion-label{
      display: none;
    }

    .infusion-field{
      margin-bottom: 15px;

      label{
        display: none;
      }

      input{
        display: block;
        width: 100%;
        height: 34px;
        padding: 6px 12px;
        font-size: 14px;
        line-height: 1.42857143;
        color: #555555;
        background: @lt-grey;
        border: 1px solid @white;
        .form-control-focus(@lt-green);
        .lh-transition(all .25s ease);
      }
    }

    .infusion-submit{
      button {
        width: 100%;
      }
    }
  }

  @media only screen and (min-width: @screen-sm-min) {
  }

  @media only screen and (min-width: @screen-md-min) {
  }

  @media only screen and (min-width: @screen-lg-min) {
  }
}


// X BROWSER /////////////////////////////////////////////////
/////////////////////////////////////////////////////////////

html.internet-explorer-9 {

  .infusion-field label {
    display: block;
  }
}